html {
  font-size: 100%;
  box-sizing: border-box;
}

*, *::after, *::before {
  box-sizing: inherit;
}

body {
  margin: 0;
  padding: 0;
  font-family: var(--fontFamilyInter);
  background-color: var(--darkBg);
  color: var(--darkWhite);
}

h1,
h2,
h3 {
  margin-top: 0;
  line-height: 1.1;
}

h1 {
  font-size: 1.5rem;
  margin-bottom: 0.1875rem;
}
@media (min-width: 71.875em) {
  h1 {
    font-size: 1.75rem;
  }
}

a,
a:visited,
a:active {
  text-decoration: none;
}

:root {
  --limeGreen: hsl(163, 72%, 41%);
  --brightRed: hsl(356, 69%, 56%);
  --facebook: hsl(208, 92%, 53%);
  --twitter: hsl(203, 89%, 53%);
  --instagramStart: hsl(37, 97%, 70%);
  --instagramEnd: hsl(329, 70%, 58%);
  --youtube: hsl(348, 97%, 39%);
  --toggleBgEnd: hsl(210, 78%, 56%);
  --toggleBgStart: hsl(146, 68%, 55%);
  --toggle: hsl(230, 22%, 74%);
  --darkBg: hsl(230, 17%, 14%);
  --darkTopBg: hsl(232, 19%, 15%);
  --darkCardBg: hsl(228, 28%, 20%);
  --darkDesaturetedBlueText: hsl(228, 34%, 66%);
  --darkWhite: hsl(0, 0%, 100%);
  --lightBg: hsl(0, 0%, 100%);
  --lightTopBg: hsl(225, 100%, 98%);
  --lightCardBg: hsl(227, 47%, 96%);
  --lightGrayishBlueText: hsl(228, 12%, 44%);
  --lightDarkBlue: hsl(230, 17%, 14%);
}

:root {
  --fontFamilyInter: "Inter", sans-serif;
}

.container {
  padding: 0 1.5625rem;
  max-width: 69.375rem;
  margin: 0 auto 2.875rem;
}
@media (min-width: 71.875em) {
  .container {
    padding: 0;
  }
}

.card {
  padding: 1.25rem;
}

.toggle {
  display: grid;
  grid-template-columns: 1fr 3.75rem 1fr;
  border: none;
  gap: 0.375rem;
  padding: 0;
}
.toggle label {
  font-size: 0.875rem;
  font-weight: 700;
  align-self: center;
  cursor: pointer;
}
.toggle label[for=dark] {
  grid-column: 1/2;
  justify-self: end;
}
.toggle label[for=system] {
  grid-column: 2/3;
  grid-row: 2/3;
  justify-self: center;
  align-self: start;
}
.toggle label[for=light] {
  grid-column: 3/4;
}
.toggle__wrapper {
  height: 1.5rem;
  grid-column: 2/3;
  grid-row: 1/2;
  position: relative;
}
.toggle input[type=radio] {
  opacity: 0;
  margin: 0;
  width: 1.25rem;
  height: 1.5rem;
  margin: 0 -0.125rem 0 -0.125rem;
  cursor: pointer;
}
.toggle input[type=radio]:focus ~ .toggle__button {
  border: 2px solid white;
}
.toggle__background {
  display: block;
  position: absolute;
  height: 100%;
  width: 100%;
  top: 0;
  border-radius: 0.75rem;
  background-image: linear-gradient(225deg, var(--toggleBgStart) 0%, var(--toggleBgEnd) 98.02%);
  pointer-events: none;
}
.toggle__button {
  position: absolute;
  left: 50%;
  right: auto;
  transform: translate(-50%);
  top: 0.1875rem;
  width: 1.125rem;
  height: 1.125rem;
  border-radius: 50%;
  background-color: var(--darkBg);
  transition: all 0.15s ease-in-out;
}
.toggle #light:checked ~ .toggle__button {
  left: calc(100% - 0.75rem);
  right: 0;
}
.toggle #dark:checked ~ .toggle__button {
  left: 0.75rem;
  right: 100%;
}

.header {
  display: flex;
  flex-wrap: wrap;
  margin-top: 2.25rem;
}
@media (min-width: 40em) {
  .header {
    justify-content: space-between;
    align-items: center;
  }
}
.header__title {
  width: 100%;
}
@media (min-width: 40em) {
  .header__title {
    width: auto;
  }
}
.header__subtitle {
  font-size: 0.875rem;
  font-weight: 700;
  color: var(--darkDesaturetedBlueText);
}